/**************************************
    Colors
**************************************/
/**************************************
    Reset and Scrollbar
**************************************/
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scrollbar-color: #111111 #f5f5f5;
  scrollbar-width: thin;
}

::-webkit-scrollbar {
  width: 1rem;
}

::-webkit-scrollbar-track {
  background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
  background-color: #111111;
  border-radius: 100vw;
  border: 0.25rem solid #f5f5f5;
}
::-webkit-scrollbar-thumb:hover {
  background-color: rgba(17, 17, 17, 0.85);
}

/**************************************
    Base Styles
**************************************/
.body {
  font-family: "League Spartan", sans-serif;
  font-weight: 300;
  font-size: 1.25rem;
  line-height: 1.5625rem;
  letter-spacing: -0.19px;
  text-align: center;
  overflow-x: hidden;
}
@media (min-width: 635px) {
  .body {
    font-size: 1.375rem;
    line-height: 1.75rem;
    letter-spacing: 0.85px;
  }
}
@media (min-width: 1440px) {
  .body {
    text-align: left;
  }
}

.container {
  width: min(100% - 3rem, 573px);
  margin-inline: auto;
}
.container--secondary {
  width: min(100% - 3rem, 689px);
  margin-inline: auto;
}
@media (min-width: 1440px) {
  .container, .container--secondary, .container--hero {
    width: 1110px;
  }
  .container--hero {
    position: relative;
    min-height: 820px;
    margin-inline: auto;
  }
}

.link {
  text-decoration: none;
  color: inherit;
}

.flip {
  transform: rotate(-180deg);
}

/**************************************
    Header
**************************************/
.hero {
  min-height: 748px;
  background-image: url(../images/homepage/hero-bg-mobile@2x.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top;
