  bottom: 0;
  left: 0;
}
.hero .wrapper .logo {
  max-width: 82.4px;
  max-height: 32px;
  margin-top: 0.75rem;
  margin-bottom: 2rem;
}
.hero__heading {
  font-weight: 200;
  font-size: 2.35rem;
  line-height: 2.25rem;
  letter-spacing: 0.8px;
  margin-bottom: 1.5rem;
}
.hero__text {
  font-weight: 200;
  max-width: 29ch;
  margin-inline: auto;
  margin-bottom: 2rem;
}
.hero .btn--dark {
  border: 1px solid #FFFFFF;
}
